import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/ProductDetails.css';  //  CSS 
import provinceDetails from '../data/provinceDetails';  // 
import { provinces } from '../data/provinces'; // 

const ProductDetails = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const province = queryParams.get('province'); // 

  const provinceData = provinces.find(p => p.name.toLowerCase() === province);

  if (!provinceData) {
    navigate('/');
    return null; //  null 
  }

  const navigateToOrderForm = () => {
    navigate(`/order?province=${province}`);
  };

  return (
    <div className="container mt-5">
      {province && (
        <div className="text-center mb-4">
          <img
            src={`${process.env.REACT_APP_BASE_URL}/images/provinces/${provinceData.img}`}
            alt={province}
            className="product-image"
          />
        </div>
      )}
      <div>
        <p className="lead"><strong></strong></p>
        <ul className="list-unstyled">
        {provinceDetails[province] && provinceDetails[province].split(/[,;]/).map((detail, index) => (
          <li key={index}>{detail}</li>
        ))}
        </ul>
        <div className="text-center mt-5 mb-5">
          <button className="btn btn-primary" onClick={navigateToOrderForm}></button>
        </div>
      </div>
    </div>
  );
}

export default ProductDetails;
